body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.title {
  background-color: red;
}
.main_dashboard li p:nth-of-type(3) {
  overflow: hidden;
  height: 100px;
}
.main_dashboard img {
  width: 100px;
  height: 100px;
  float: left;
}
.main_dashboard div {
  width: 90%;
  float: right;
}
.main_dashboard li {
  height: 100px;
  display: block;
}
ul.categorie li {
  display: -webkit-box;
}
ul.categorie {
  width: 30%;
  list-style: none;
  float: left;
}
.width {
  display: flex;
}
